<template>
  <div class="notice-view">
    <header-view :title="title" :routerName="routerName"></header-view>
    <section class="notice-list">
      <div class="notice-item" v-for="(item,key) in noticeJson" @click="toNoticeContent(item.id,item.noticeTitle,item.noticeContent)">
        <h3 class="notice-title"><span class="notice-name">{{item.noticeTitle}}</span><time class="pub-date">{{item.createTime}}</time></h3>
        <article class="item-summary">{{item.summary}}</article>
      </div>
    </section>
  </div>
</template>

<script>
  import {getNoticeList} from '../../service/getData'
  import header from '@/components/header/header'
  export default {
    data () {
      return {
        noticeJson: null,
        title: '公告',
        routerName: 'home'
      }
    },
    created: function () {
      var _this = this
      getNoticeList()
        .then(function (response) {
          _this.noticeJson = response.data
        })
        .catch(function () {
          _this.$toast('连接服务器异常！')
        })
    },
    methods: {
      toNoticeContent: function (id,noticeTitle,noticeContent) {
        this.$router.push({
          name: 'noticeContent',
          params: {
            id: id,
            noticeTitle: noticeTitle,
            noticeContent: noticeContent
          }
        })
      }
    },
    components: {
      'header-view': header
    }
  }
</script>

<style>
/* 公告页 */

.notice-view {
  height: 100%;
  width: 100%;
  padding-top: .4rem;
}

.notice-list {
  height: 100%;
  padding: 0 0 0 .1rem;
  padding-bottom: .2rem;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

.notice-item {
  height: .9rem;
  padding: .1rem 0;
  padding-right: .1rem;
  border-bottom: .01rem solid lightgray;
}

.notice-title {
  display: flex;
  color: #333;
  align-items: flex-end;
  font-size: .14rem;
}

.notice-title .notice-name {
  flex: auto;
  padding-right: .1rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.notice-title time {
  color: #999999;
  width: 1.2rem;
  text-align: right;
  vertical-align: bottom;
  white-space: nowrap;
  font-size: .12rem;
  font-weight: normal;
}

.item-summary {
  font-size: .12rem;
  margin-top: .1rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
</style>


